html {
  font-size: 100%;
  box-sizing: border-box;
  height: 100%;
}

*, *::before, *::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
  height: 100%;
  background-color: #1f1f1f;
  font-family: "Roboto", sans-serif;
}

h1,
h2,
h3 {
  margin: 0;
}

a,
a:visited,
a:active {
  font-size: 1rem;
  font-weight: 300;
  text-decoration: none;
}

h1 {
  font-size: 3rem;
  font-weight: 400;
}

h2 {
  font-size: 2.1875rem;
  font-weight: 300;
}

h3 {
  font-size: 1.25rem;
  font-weight: 400;
}

p,
span,
button {
  font-size: 1rem;
  font-weight: 200;
}

.navbar {
  display: flex;
  height: 3.4375rem;
  justify-content: space-between;
  align-items: center;
  width: 100vw;
  padding: 0 3.125rem;
  color: #1f1f1f;
}
.navbar h1 {
  font-size: 1.875rem;
  color: #FFD673;
}
.navbar input {
  height: 2.5rem;
  width: 30rem;
  padding: 0.688rem 0.938rem;
  border: none;
  border-radius: 0.75rem;
}
.navbar .login-btn-parent span {
  color: #80e897;
}
.navbar .login-btn-parent button {
  height: 2.5rem;
  width: 6.875rem;
  background-color: #80e897;
  margin-left: 1.375rem;
  border: none;
  border-radius: 3.125rem;
}

section.hero-section {
  display: flex;
}
section.hero-section .hero-text {
  width: 50%;
  color: #ffffff;
}
section.hero-section .hero-text span.emphasized {
  font-size: 3rem;
  font-weight: 400;
  color: #FFD673;
}
section.hero-section .hero-text p {
  margin: 3.75rem 0;
}
section.hero-section .hero-text .login-btn-parent span {
  color: #80e897;
}
section.hero-section .hero-text .login-btn-parent button {
  height: 2.5rem;
  width: 6.875rem;
  background-color: #80e897;
  margin-right: 1.375rem;
  border: none;
  border-radius: 3.125rem;
}